<template>
  <v-card>
    <v-card-title>
      <v-btn color="primary" @click="addBrand"></v-btn>
      <!--search-->
      <v-spacer/>
      <v-flex xs3>
      <v-text-field label="" v-model.lazy="search" append-icon="search" hide-details/>
      </v-flex>
    </v-card-title>
    <v-divider/>
    <v-data-table
      :headers="headers"
      :items="brands"
      :pagination.sync="pagination"
      :total-items="totalBrands"
      :loading="loading"
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <td class="text-xs-center">{{ props.item.id }}</td>
        <td class="text-xs-center">{{ props.item.name }}</td>
        <td class="text-xs-center">
          <img v-if="props.item.image" :src="props.item.image" width="130" height="40">
          <span v-else></span>
        </td>
        <td class="text-xs-center">{{ props.item.letter }}</td>
        <td class="justify-center layout px-0">
          <v-btn icon @click="editBrand(props.item)">
            <i class="el-icon-edit"/>
          </v-btn>
          <v-btn icon @click="deleteBrand(props.item)">
            <i class="el-icon-delete"/>
          </v-btn>
        </td>
      </template>
      <template v-slot:pageText="props">
         {{ props.pageStart }} - {{ props.pageStop }}  {{ props.itemsLength }}
      </template>
    </v-data-table>
    <!---->
    <v-dialog max-width="500" v-model="show" persistent scrollable>
      <brand-form @close="closeWindow" :oldBrand="oldBrand" :isEdit="isEdit"/>
    </v-dialog>
  </v-card>
</template>

<script>
  // 
  import BrandForm from './BrandForm'

  export default {
    name: "brand",
    data() {
      return {
        search: '', // 
        totalBrands: 0, // 
        brands: [], // 
        loading: true, // 
        pagination: {}, // 
        headers: [
          {text: 'id', align: 'center', value: 'id'},
          {text: '', align: 'center', sortable: false, value: 'name'},
          {text: 'LOGO', align: 'center', sortable: false, value: 'image'},
          {text: '', align: 'center', value: 'letter', sortable: true,},
          {text: '', align: 'center', value: 'id', sortable: false}
        ],
        show: false,// 
        oldBrand: {}, // 
        isEdit: false, // 
      }
    },
    mounted() { // 
      // 
      this.getDataFromServer();
    },
    watch: {
      pagination: { // pagination
        deep: true, // deeptruepagination
        handler() {
          // getDataFromServer
          this.getDataFromServer();
        }
      },
      search: { // 
        handler() {
          this.getDataFromServer();
        }
      }
    },
    methods: {
      getDataFromServer() { // 
        // 
        this.$http.get("/item/brand/page", {
          params: {
            key: this.search, // 
            page: this.pagination.page,// 
            rows: this.pagination.rowsPerPage,// 
            sortBy: this.pagination.sortBy,// 
            desc: this.pagination.descending// 
          }
        }).then(resp => { // 
          this.brands = resp.data.items;
          this.totalBrands = resp.data.total;
          // false
          this.loading = false;
        })
      },
      addBrand() {
        // 
        this.isEdit = false;
        // 
        this.show = true;
        // oldBrandnull
        this.oldBrand = null;
      },
      editBrand(oldBrand){
        // 
        this.$http.get("/item/category/of/brand/?id="+oldBrand.id)
          .then(({data}) => {
            // 
            this.isEdit = true;
            // 
            this.show = true;
            // brand
            this.oldBrand = oldBrand
            // 
            this.oldBrand.categories = data;
          })
      },
      closeWindow(){
        // 
        this.getDataFromServer();
        // 
        this.show = false;
      }
    },
    components:{
        BrandForm
    }
  }
</script>

